import Layout from '../components/Layout';

const Index = () => (
    <Layout  title="himanshu pandey <code>javascript enthusiast </code>">
        <p style={{color: "gray"}}>A FULL STACK DEVELOPER, UI ENGINEER, PERFORMANCE OPTIMIZER, WITH AN OBJECTIVE OF MAKING WEB MORE USABLE OVER TIME.</p>
        <embed>
            {
                Array.prototype.myreduce = function(func, result) {
                    let i = 0;
                    if (arguments.length < 2) {
                        i = 1;
                        result = this[0];
                    }
                    for (; i < this.length; i++) {
                        result = func(result, this[i], i, this);
                    }
                    return result;
                }
            }
        </embed>
        <img src={'./static/javascript-logo.png'} alt="js" height="100px"/>
    </Layout>
);

export default Index;